.profileSectionBackgroundBackdrop{
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.8);
    position: fixed;
    top: 0;
    right: 0;
    z-index: 101;
    cursor: pointer;
    .profileSectionBackgroundEditor{
        cursor: auto;
        position: absolute;
        top: 10%;
        left: 15%;
        z-index: 102;
        background-color: white;
        width: 70vw;
        height: 80vh;
        margin: auto;
        border-radius: 10px;
        padding: 1rem;
        @media screen and (max-width: 450px){
            top: 0;
            left: 0;
            border-radius: 0;
            width: 100vw;
            position: fixed;
            height: 100vh;
        }
        .profileSectionBackgroundEditorHeader{
            display: flex;
            justify-content: space-between;
            align-items: center;
            .profileSectionBackgroundEditorHeading{
                font-weight: 400;
                font-size: 1.5rem;
                @media screen and (max-width: 525px){
                    font-size: 1.2rem;
                }
            }
        }
        .backgroundLoaderLayout{
            height: 55vh;
            margin: 10px;
            overflow-y: scroll;
            @media screen and (max-height: 566px){
                height: 90vh;
            }
            .preLoadedBackGroundsSelector{
                padding: 1rem;
                .preLoadedBackGroundsSelectorHeader{
                    font-size: 1.2rem; 
                }
                .preLoadedBackGroundsSelectorOptionDiv{
                    padding: 5% 5% 5% 5%;
                    width: 100%;
                    height: 20%;
                    cursor: pointer;
                    .preLoadedBackGroundsSelectorOption{
                        width: 100%;
                        height: 100%;
                        border: black 2px solid;
                    }
                    &:hover{
                        border: rgba(0, 0, 0, 0.2) 1px solid;
                    }
                }
                .preLoadedBackGroundsSelectorOptionDivSelected{
                    border: rgba(0, 0, 0, 1) 2px solid !important;
                }
            }
            .preLoadedBackGroundsSelectorCrop{
                width: 90%;
                margin: auto;
            }
        }
    }
}