//social media icons

.aboutSectionSocialMediaLinks .btn,
.aboutSectionSocialMediaLinks .btn:before,
.aboutSectionSocialMediaLinks .btn .fab {
	transition: all 0.35s;
	transition-timing-function: cubic-bezier(0.31, -0.105, 0.43, 1.59);
}
.aboutSectionSocialMediaLinks {
	height: fit-content;
	margin: auto;
	font-size: 0;
	text-align: center;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
	.btn {
		vertical-align: middle;
        display: flex;
        align-items: center;
        justify-content: center;
		background-color: #fff;
		width: 70px;
		height: 70px;
		line-height: 90px;
		margin: 1rem;
		text-align: center;
		position: relative;
		overflow: hidden;
		border-radius: 28%;
		box-shadow: 0 10px 20px rgba(0,0,0,0.19), 0 6px 6px rgba(0,0,0,0.23);
		opacity: 0.99;
		&:before {
			top: 90%;
			left: -110%;
			content: '';
			width: 120%;
			height: 120%;
			position: absolute;
			transform: rotate(45deg);
		}
		.fab {
			transform: scale(0.8);
			font-size: 38px;
			vertical-align: middle;
		}
		&.facebook {
			&:before {
				background-color: #3b5998;
			}
			.fab {
				color: #3b5998;
			}
		}
		&.twitter {
			&:before {
				background-color: #3cf;
			}
			.fab {
				color: #3cf;
			}
		}
		&.google {
			&:before {
				background-color: #dc4a38;
			}
			.fab {
				color: #dc4a38;
			}
		}
		&.linkedIn {
			&:before {
				background-color: #0A66C2;
			}
			.fab {
				color: #0A66C2;
			}
		}
		&.gitHub {
			&:before {
				background-color: #0D1117;
			}
			.fab {
				color: #0D1117;
			}
		}
		&.instagram {
			&:before {
				background-color: #d52876;
			}
			.fab {
				color: #d52876;
			}
		}
	}
}
.aboutSectionSocialMediaLinks .btn:focus:before,
.aboutSectionSocialMediaLinks .btn:hover:before {
	top: -10%;
	left: -10%;
}
.aboutSectionSocialMediaLinks .btn:focus .fab,
.aboutSectionSocialMediaLinks .btn:hover .fab {
	color: #fff;
	transform: scale(1);
}
